<template>
  <div class="container">
    <div class="searchs">
      <el-form :inline="true" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item>
              <el-button
                type="success"
                :loading="export_file_loading"
                @click="importUser"
                >导出用户</el-button
              >
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="文件ID" align="center" />
      <el-table-column prop="type_name" label="文件类型" align="center" />
      <el-table-column prop="file" label="文件名" align="center">
        <template slot-scope="scope"
          ><a target="_blank" :href="scope.row.file">{{
            scope.row.file
          }}</a></template
        >
      </el-table-column>
      <el-table-column prop="addtime_date" label="生成时间" align="center" />
      <!-- <el-table-column
              prop="address"
              align="center"
              label="操作"
            >
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="primary" size="small">查看</el-button>
              </template>
            </el-table-column> -->
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      tableData: [],
      count: 0,
      page_curren: 1,
      loading: false,
      export_file_loading: false,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    importUser() {
      Fetch('/sys/export/export', { type: 'user' }).then(() => {
        this.export_file_loading = true
        this.loadingfile()
      })
    },
    loadingfile() {
      var timer = setInterval(() => {
        Fetch('/sys/export/exportStatus', { type: 'user' }).then(res => {
          if (!res.data.is_loading) {
            clearInterval(timer)
            this.export_file_loading = false
            this.FetchList()
          }
        })
      }, 1000)
    },
    FetchList() {
      this.loading = true
      Fetch('/sys/export/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.export_file_loading = r.data.export_file_loading
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
  },
}
</script>
